html{box-sizing:border-box;height:100%}*,:after,:before{box-sizing:inherit}body{display:flex;align-items:center;justify-content:center;min-height:100%;margin:0;padding:0;background:#36d1dc;background:-webkit-linear-gradient(90deg,#5b86e5,#36d1dc);background:linear-gradient(90deg,#5b86e5,#36d1dc);font-family:Helvetica Neue,Helvetica,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.content{width:400px;text-align:center;transform:translateY(-5%)}.avatar{display:inline-block;border-radius:50%;border:4px solid #fff;transition:transform .5s ease-in-out;box-shadow:0 0 10px 2px rgba(0,0,0,.2);background-image:url(https://www.gravatar.com/avatar/52869e2103531ad458ab57bf53b29679?s=240);background-size:cover;width:120px;height:120px}.avatar img{display:none}.avatar:active{transform:rotate(1turn)}.title h2{font-size:28px;font-weight:200;letter-spacing:2px;color:#fff;margin:40px 0 20px}.social-icons a{display:inline-block;margin:0 10px;text-decoration:none;color:#fff;font-size:32px;transition:transform .25s ease-in-out,text-shadow .25s ease-in-out}.social-icons a:active{transform:scale(1.2);text-shadow:0 0 5px rgba(0,0,0,.2)}@media only screen and (min-width:961px){.avatar:hover{transform:rotate(1turn)}.social-icons a:hover{transform:scale(1.2);text-shadow:0 0 5px rgba(0,0,0,.2)}}